
import { defineComponent } from "vue";
import DataFormatService from "@/shared/services/DataFormatService";
import { TRN24500111_REQ_BODY, TRN24500111_RES, FUND_TRANSFER_LIST } from '@/shared/type/TRN24500111.type';
import Pagination from "@/shared/components/Pagination/Pagination.vue";
import { PAGE_SIZE } from "@/shared/constants/common.const";
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue";
import '@vuepic/vue-datepicker/dist/main.css'
import VueDatePicker from '@vuepic/vue-datepicker';
import { ExportExcel } from "@/shared/services/ExportExcelService";
import { ExportSheet } from "@/shared/type/ExportExcel.type";

const dataFormatService = new DataFormatService();
const excel = new ExportExcel()

export default defineComponent({
	name: "trn245001sc00",
	components: {
		Pagination,
		BreadCrumb,
		SelectOption,
		VueDatePicker
	},
	data() {
		return {
			dataTable: [] as FUND_TRANSFER_LIST[],
			rawDataTable: [] as FUND_TRANSFER_LIST[],
			searchField: {
				workProgressStausCode: this.$route.query.filter as any,
				customerName: "",
				applicationTypeCode: "",
				serverTransactionID: '',
				senderAccountNo: '',
				beneficiaryAccountNumber: '',
				transferCurrencyCode: "",
				applyStartDate: '' as unknown,
				applyEndDate: '' as unknown
			},
			totalItems: 0,
			currentPage: 1,
			pageSize: PAGE_SIZE,
			startingIndex: 1,
			resetPage: false,
		};
	},

	mounted() {
		this.inquiryFundTransfer(false);
	},

	computed: {
		headers() {
			return [
				{ columnName: "No." },
				{ columnName: "View" },
				// { columnName: "Request Type" },
				{ columnName: "Transaction Ref." },
				{ columnName: "Transaction Date" },
				{ columnName: "Transfer Amount" },
				{ columnName: "Fee Amount" },
				{ columnName: "From Account No." },
				{ columnName: "From Account Name" },
				{ columnName: "Beneficiary Account" },
				{ columnName: "Beneficiary Account Name" },
				{ columnName: "Beneficiary Bank" },
				{ columnName: "Status" },
				{ columnName: "Remarks" },
				{ columnName: "Update Date" },
				{ columnName: "Update By" },
			]
		},
		formattedStartDate(): string {
			return this.searchField.applyStartDate ? this.$datetime.formatVueDatePicker(this.searchField.applyStartDate as Date) : ''
		},
		formattedEndDate(): string {
			return this.searchField.applyEndDate ? this.$datetime.formatVueDatePicker(this.searchField.applyEndDate as Date) : ''
		}
	},

	methods: {

		async exportToExcel() {
			const customHeader = [
				// 'Request Type',
				'Transaction Ref.',
				'Transaction Date',
				'Transfer Amount',
				'Fee Amount',
				'From Account No.',
				'From Account Name',
				'Beneficiary Account',
				'Beneficiary Account Name',
				'Beneficiary Bank',
				'Status',
				'Approval No',
				'Approval Request Date',
				'Remarks',
				'Update Date',
				'Update By',
			]

			const reqBody: TRN24500111_REQ_BODY = {
				workProgressStatusCode: this.searchField.workProgressStausCode,
				customerName: this.searchField.customerName,
				applicationTypeCode: this.searchField.applicationTypeCode,
				fromTransactionDate: this.searchField.applyStartDate ? this.searchField.applyStartDate ? this.$datetime.convertDateToString(this.searchField.applyStartDate as Date) : '' : '',
				transferCurrencyCode: this.searchField.transferCurrencyCode,
				toTransactionDate: this.searchField.applyEndDate ? this.searchField.applyEndDate ? this.$datetime.convertDateToString(this.searchField.applyEndDate as Date) : '' : '',
				senderAccountNo: this.searchField.senderAccountNo,
				beneficiaryAccountNumber: this.searchField.beneficiaryAccountNumber,
				serverTransactionID: this.searchField.serverTransactionID,
				pageNumber: 0,
				pageSize: this.totalItems
			};

			const res = await this.$request.getRequest("TRN24500111", reqBody, false) as TRN24500111_RES;

			const tableForExport = this.convertCodeToValue(res.body.list)

			const dataForExport: ExportSheet = {
				data: tableForExport,
				columnHeader: [customHeader],
				sheetName: 'International Fund Transfer'
			}

			excel.exportSheet([dataForExport], 'International Fund Transfer Request')
		},

		async inquiryFundTransfer(isSearch: boolean) {
			const reqBody: TRN24500111_REQ_BODY = {
				workProgressStatusCode: this.searchField.workProgressStausCode,
				customerName: this.searchField.customerName,
				applicationTypeCode: this.searchField.applicationTypeCode,
				fromTransactionDate: this.searchField.applyStartDate ? this.searchField.applyStartDate ? this.$datetime.convertDateToString(this.searchField.applyStartDate as Date) : '' : '',
				transferCurrencyCode: this.searchField.transferCurrencyCode,
				toTransactionDate: this.searchField.applyEndDate ? this.searchField.applyEndDate ? this.$datetime.convertDateToString(this.searchField.applyEndDate as Date) : '' : '',
				senderAccountNo: this.searchField.senderAccountNo,
				beneficiaryAccountNumber: this.searchField.beneficiaryAccountNumber,
				serverTransactionID: this.searchField.serverTransactionID,
				pageNumber: isSearch ? 0 : this.currentPage,
				pageSize: this.pageSize,
			};

			if (isSearch) {
				this.currentPage = 1
				this.resetPage = true;
			}

			const res = await this.$request.getRequest("TRN24500111", reqBody, false) as TRN24500111_RES;

			if (res.header.result) {

				this.rawDataTable = res.body.list
				this.dataTable = JSON.parse(JSON.stringify(this.convertCodeToValue(res.body.list)))
				this.totalItems = res.body.totalCount;
			}
		},

		convertCodeToValue(data: FUND_TRANSFER_LIST[]) {
			return data.map((item: FUND_TRANSFER_LIST) => {
				return {
					// applicationTypeCode: `${item.applicationTypeCode} : ${this.$codeConverter.convertValue('ApplicationTypeCode', item.applicationTypeCode)}`,
					serverTransactionID: item.serverTransactionID,
					transactionDate: this.$datetime.formatStringToStringDate(item.transactionDate),
					transferAmount: this.$format.formatCurrency(item.transferAmount) + (' ') + (item.transferCurrencyCode),
					feeAmount: this.$format.formatCurrency(item.feeAmount, item.transferCurrencyCode),
					senderAccountName: item.senderAccountName,
					senderAccountNo: this.$format.formatAccountNumber(item.senderAccountNo),
					beneficiaryAccountNumber: dataFormatService.formatAccountNumber(item.beneficiaryAccountNumber),
					beneficiaryName: item.beneficiaryName,
					beneficiaryBankName: item.beneficiaryBankName,
					workProgressStatusCode: `${item.workProgressStatusCode} : ${this.$codeConverter.convertValue('WorkProgressStatusCode', item.workProgressStatusCode)}`,
					internetBankingApprovalNo: item.internetBankingApprovalNo,
					approvalRequestDate: this.$datetime.formatStringToStringDate(item.approvalRequestDate),
					remark: item.remark,
					lastChangeDate: this.$datetime.formatStringToStringDate(item.lastChangeDate),
					lastChangeTeller: this.displayTellerNameID(item.lastChangeTellerName, item.lastChangeTellerID)			
				}
			})
		},

		handleRowClick(index: number, rowData: FUND_TRANSFER_LIST) {
			this.$router.push({
				path: 'TRN245001SC01',
				query: {
					type: 'edit',
					index: index,
					approvalRequestDate: this.$datetime.unFormatStringToStringDate(rowData.approvalRequestDate),
					internetBankingApprovalNo: rowData.internetBankingApprovalNo,
				}
			});
		},

		handlePagination(pageNumber: number) {
			this.currentPage = pageNumber;
			this.resetPage = false;
			this.inquiryFundTransfer(false);
		},

		handleStartingIndex(startingIndex: number) {
			this.startingIndex = startingIndex;
		},

		handleRefresh() {
			this.searchField.customerName = "";
			this.searchField.workProgressStausCode = "";
			this.searchField.applicationTypeCode = "";
			this.searchField.transferCurrencyCode = "";
			this.searchField.serverTransactionID = '';
			this.searchField.senderAccountNo = '';
			this.searchField.beneficiaryAccountNumber = '';
			this.searchField.applyStartDate = "";
			this.searchField.applyEndDate = "";
			this.currentPage = 1
			this.resetPage = true;
			this.inquiryFundTransfer(false);
		},

		displayTellerNameID(name: string, id: string): string {
			return name.trim() !== '' ? id.trim() !== '' ? `${name} (${id})` : `${name}` : `${id}`
		}
	},
});
